import { useState } from "react";
import { useRef } from "react";
import {uploadFileandGetLink} from "../../../../services/resourceServices"
const Lesson = (props)=>{
  
    const [lessonTitle, setlessonTitle] = useState();
    const [lessonDescription, setLessonDescription] = useState();
    const [videos, setVideos] = useState([1]);
    const [videodata, setVideodata] = useState([]);
    const [videoattachdata, setVideoattachdata] = useState([]);
    const [uploadingvideo, setUploadingvideo] = useState([]);
    const [uploadingvideoattach, setUploadingvideoattach] = useState([]);
    const uploadVideoRef = useRef();
    const uploadVideoattachRef = useRef();

    
    const addVideo = ()=>{
      setVideos((videos)=>[...videos,videos.length+1]);
    }

    async function uploadVideo(e,index){
      console.log(index)
      const load = [...uploadingvideo]
      load[index] = true;
      setUploadingvideo(load);
      const video = await uploadFileandGetLink(e.target.files[0])
      var vd = [...videodata];
      vd[index] = video;
      var vdnew = [...vd];
      setVideodata(vdnew);
      sessionStorage.setItem('videodata',vdnew);
      
      const load1 = [...uploadingvideo]
      load1[index] = false;
      setUploadingvideo(load1)
    }

    async function uploadVideoattach(e,index){
      const load = [...uploadingvideoattach]
      load[index] = true;
      setUploadingvideoattach(load);
      const video = await uploadFileandGetLink(e.target.files[0])
      var vd = [...videoattachdata];
      vd[index] = video;
      var vdnew = [...vd];
      setVideoattachdata(vdnew);
      sessionStorage.setItem('videoattachdata',vdnew);
      
      const load1 = [...uploadingvideoattach]
      load1[index] = false;
      setUploadingvideoattach(load1)
    }
    
    function openFileDialog(){
      uploadVideoRef.current.click();
    }
    function openFileDialog1(){
      uploadVideoattachRef.current.click();
    }
    console.log(videodata);
    return <div className="mt-20">
            <div className="module-header flex space-between remove-lesson-button" style={{justifyContent:'flex-end'}}>
              <p>Remove Lesson</p>
              <img src="/Images/cancel-button.svg" alt="cancel-button"/>
            </div>
              <input className="cruspo-input mt-10" placeholder="Give your lesson a title" onChange={(e)=>setlessonTitle(e.currentTarget.value)}/>
              <textarea className="cruspo-textarea" placeholder="Give your lesson a description" onChange={(e)=>setLessonDescription(e.currentTarget.value)}></textarea>
             {
               videos.map((v,index)=>
                 <>
                  <div className="flex">
                    <div className="upload-video-container" style={{flexBasis:'60%'}}>
                      <input type="file" ref={uploadVideoRef} style={{display:'none'}} onChange={(e)=>uploadVideo(e,index)} />
                      <p style={{margin:'10px 0',fontSize:'20px'}}>Upload your video here </p>
                      <button className="upload-video-button" type="button" onClick={()=>openFileDialog()}>{uploadingvideo[index] ? 'Uploading...' : videodata[index]?.length ? <video src={videodata}></video> : 'Upload'}</button>
                    </div>
                    <div className="upload-attachment-container" style={{flexBasis:'40%'}}>
                    <input type="file" ref={uploadVideoattachRef} style={{display:'none'}} onChange={(e)=>uploadVideoattach(e,index)} />
                      <p style={{margin:'10px 0',fontSize:'20px'}}>Upload your video attachment here</p>
                      <button className="upload-attachment-button" onClick={()=>openFileDialog1()} type="button">{uploadingvideoattach[index] ? 'Uploading...' : videoattachdata[index]?.length ? "Uploaded" : 'Upload'}</button>
                    </div>
                  </div>
                  <textarea className="cruspo-textarea" placeholder="Describe your course video"></textarea>
                </>
               )
              }
              <div className="flex space-between  add-video-button" style={{width:'10%',borderRadius:'50px',marginBottom:'20px'}} onClick={()=>addVideo()}>
                <p>Add Video</p>
                <img src="/Images/add-member.svg" alt="alt"/>
              </div>
        </div>
  }
  export default Lesson;